* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 16px;
  color: #fff;
}
.container {
  width: 800px;
  display: flex;
  flex-direction: column;
}
.App {
  background-image: url("./assets/images_background.jpeg");
  background-repeat: no-repeat;
  background-size: cover;

  height: 100vh;

  display: flex;
  justify-content: center;
}
